@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.brands {
  align-items: center;
  background-color: #F9F9F9;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .brands__title {
    color: #808080;
    font-family: "Lato", sans-serif;
    font-size: 18px;
    font-weight: 300;
    padding-top: 40px;
    text-align: center; }
  .brands__logos {
    margin: 20px 0;
    flex-wrap: wrap; }
  .brands__logo {
    margin: 0 10px;
    width: 100px; }
    @media screen and (min-width: 770px) {
      .brands__logo {
        margin: 0 20px;
        width: 200px; } }



