@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.label {
  color: #FFF;
  font-family: "Lato", sans-serif;
  font-size: 13px;
  text-transform: uppercase; }

.white {
  background-color: transparent;
  border: 2px #FFF solid;
  border-radius: 30px;
  cursor: pointer;
  line-height: 40px;
  margin: 0 20px 0 15px;
  transition: background-color 0.5s ease;
  width: 120px; }
  .white:focus {
    outline: 0; }
  .white:hover, .white:focus, .white:active {
    background-color: #FFF; }
    .white:hover .label, .white:focus .label, .white:active .label {
      color: #0fc7f4;
      font-weight: 900; }

.blue {
  background-color: #40d2f6;
  border: none;
  border-radius: 30px;
  color: #FFF;
  cursor: pointer;
  line-height: 40px;
  margin: 30px 20px 0 15px;
  padding: 10px 40px;
  transition: background-color 0.5s ease; }
  .blue:focus {
    outline: 0; }
  .blue:hover, .blue:focus, .blue:active {
    background-color: #088dae; }
  .blue .label {
    font-family: "Lato", sans-serif;
    font-size: 16px;
    font-weight: 300px;
    letter-spacing: 3px; }



